* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.App {
  width: 100%;
  height: 100vh;
  background: linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.6)),url("./static/4.jpg");
}


.App_main {
  color: white;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  
}


#search {
  padding: 10px;
  background: none;
  color: white;
  border: 2px solid white;
  width: 70%;
  height: 10%;
  font-size: 25px;
  margin: 20px;
  border-radius: 10px;
  text-indent: 10px;
  letter-spacing: 1px;
}


#searchBtn {
  letter-spacing: 2px;
  background-color: white;
  padding: 10px 30px;
  font-size: 20px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  transition: 0.2s ease-in-out all;
}


#searchBtn:hover {
  background-color: orangered;
  color: white;
}


#changeBackground {
  position: relative;
  top: -40px;
  float: right;
  margin: 10px;
  background: none;
  color: white;
  text-decoration: underline;
  border: none;
  cursor: pointer;
}

#time {
  letter-spacing: 3px;
  font-size: 90px;
  text-transform: uppercase;
}

#greetings {
  font-size: 40px;
}

#greet {
  color: orangered;
}